.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #1f291f;
  height: 49px;
}

.toolbar__navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.toolbar__logo {
  margin-left: 1rem;
}

.toolbar__logo a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
}

.spacer {
  flex: 1;
}

.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.toolbar_navigation-items li {
  padding: 0 0.5rem;
}

.toolbar_navigation-items a {
  color: white;
  text-decoration: none;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: rgb(197, 124, 56);
}


.main {
  margin-top: 56px;

}

@media (min-width: 768px) {
  .main {
    margin-left: 10%;
    margin-right: 10%;
  }
}

@media (min-width: 425px) {
  .main {
    margin-top: 60px;
    margin-left: 10%;
    margin-right: 10%;
  }
}

@media (min-width: 569px) {

}

.text-field {
  border-style: dotted;
  padding: 5px;

}

.body,html {
  height: 100%;
}
.bg {

  background-image: url("./components/background_pic.jpg");
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}